<template>
  <div class="container">
    <nav-bar style="margin-bottom: 40px" />
    <router-link :to="{ name: 'menus page' }" class="btn btn-primary ms-2"
      >go to menus</router-link
    >
    <form class="feilds-con text-capitalize" @click.prevent>
      <div class="form-floating mb-3">
        <input
          type="text"
          class="form-control"
          id="floatingInput"
          placeholder="item name"
          v-model="itemName"
          autofocus
        />
        <label for="floatingInput">item name is</label>
        <div class="alert alert-danger text-center" v-if="v$.itemName.$error">
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <input
          type="number"
          class="form-control"
          id="floatingPassword"
          placeholder="itme price"
          v-model="itemPrice"
        />
        <label for="floatingPassword">item price is</label>
        <div class="alert alert-danger text-center" v-if="v$.itemPrice.$error">
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <input
          type="number"
          class="form-control"
          id="floatingPassword"
          placeholder="item quantities"
          v-model="itemQuantities"
        />
        <label for="floatingPassword">item quantities is</label>
        <div
          class="alert alert-danger text-center"
          v-if="v$.itemQuantities.$error"
        >
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <textarea
          class="form-control h200"
          placeholder="item descibtion"
          id="floatingTextarea"
          v-model="itemDescription"
        ></textarea>
        <label for="floatingTextarea">item description is</label>
        <div
          class="alert alert-danger text-center"
          v-if="v$.itemDescription.$error"
        >
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <select
          class="form-select"
          id="floatingSelect"
          aria-label="Floating label select example"
          v-model="pickedCategory"
        >
          <option :value="cat.id" v-for="(cat, i) in categoriesArr">{{ cat.name }}</option>
        </select>
        <label for="floatingSelect">item category is</label>
        <div
          class="alert alert-danger text-center"
          v-if="v$.pickedCategory.$error"
        >
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <button
        class="click-enter text-capitalize btn-warning w-100 add-item-btn mb-3 border"
        @click="addItem()"
      >
        add item
      </button>
      <div v-if="successMessage != ''" class="alert alert-success text-center">
        {{ successMessage }}
      </div>
      <div v-if="errorMessage != ''" class="alert alert-danger text-center">
        {{ errorMessage }}
      </div>
    </form>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import useVuelidate from "@vuelidate/core";
import { mapActions } from "vuex";
import { required, maxLength, minValue } from "@vuelidate/validators";
import axios from "axios";

export default {
  name: "AddItems",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useVuelidate(),
      itemName: "",
      itemPrice: "",
      itemQuantities: "",
      itemDescription: "",
      pickedCategory: "",
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      successMessage: "",
      errorMessage: "",
      categoriesArr: [],
    };
  },
  validations() {
    return {
      itemName: { required },
      itemPrice: { required, minValue: minValue(0) },
      itemQuantities: { required, minValue: minValue(1) },
      itemDescription: { required, maxLength: maxLength(50) },
      pickedCategory: { required },
    };
  },
  methods: {
    ...mapActions(["clickEnter"]),
    async addItem() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let checkItem = await axios.get(
          `http://localhost:3000/items?name=${this.itemName}&categoryId=${this.pickedCategory}`
        );
        if (checkItem.status == 200) {
          if (checkItem.data.length != 1) {
            let item = await axios.post(`http://localhost:3000/items`, {
              name: this.itemName,
              price: this.itemPrice,
              quantities: this.itemQuantities,
              describtion: this.itemDescription,
              categoryId: this.pickedCategory,
              userId: this.userId,
              restaurantId: this.restaurantId,
            });
            if (item.status == 201) {
              this.successMessage = "added successfully";
              this.errorMessage = "";
              setTimeout(() => {
                this.$router.push({ name: "menus page" });
              }, 800);
            }
          } else {
            this.errorMessage = "dublicated item";
          }
        }
      }
    },
    async getCategories() {
      let categories = await axios.get(`http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`);
      if (categories.status == 200 && categories.data.length > 0) {
        this.categoriesArr = categories.data;
      }
    }
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (storageUser) {
      this.userId = storageUser[0].id;
      this.getCategories();
    } else {
      this.$router.push({ name: "login page" });
    }
    this.clickEnter()
  },
};
</script>

<style lang="scss" scoped>
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  .feilds-con {
    width: 270px;
    margin: -40px auto 0;
  }
  .h200 {
    min-height: 200px;
  }
  .add-item-btn {
    height: 40px;
  }
}
</style>

