<template>
  <div class="container">
    <div class="feilds-container">
    <div class="icon">
      <font-awesome-icon icon="right-to-bracket" class="login" />
    </div>
    <h3>login to your account</h3>
    <div class="user-name">
      <font-awesome-icon icon="user" />
      <input type="text" placeholder="enter your username" v-model="userName" id="user-name-field-login"/>
      <span v-if="v$.userName.$error" class="error-message">{{
        v$.userName.$errors[0].$message
      }}</span>
    </div>
    <div class="password">
      <font-awesome-icon icon="lock" />
      <input
        v-if="!passStatus"
        type="password"
        placeholder="enter your password"
        v-model="password"
      />
      <input
        v-if="passStatus"
        type="text"
        placeholder="enter your password"
        v-model="password"
      />
      <span v-if="v$.password.$error" class="error-message">{{
        v$.password.$errors[0].$message
      }}</span>
      <div v-if="!passStatus" @click="changePassfeild(true)">
        <font-awesome-icon icon="eye" class="eye show" />
      </div>
      <div v-if="passStatus" @click="changePassfeild(false)">
        <font-awesome-icon icon="eye-slash" class="eye hide" />
      </div>
    </div>
    <div class="submit">
      <button @click="login()" class="login click-enter">login</button>
    </div>
    <p class="redirect">
      I don't have an account, go to
      <router-link :to="{name: 'sign up page'}" class="text-decoration-none">sign up</router-link> page
    </p>
    <span class="alert alert-danger" v-if="unCorrectData != ''">{{
      unCorrectData
    }}</span>
    <span class="alert alert-success" v-if="correctData != ''">{{
      correctData
    }}</span>
  </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import useValidate from "@vuelidate/core";
import { required, maxLength, minLength } from "@vuelidate/validators";
import axios from "axios";

export default {
  name: "loginView",
  computed: {
    ...mapState(["passStatus"]),
  },
  methods: {
    ...mapActions(["redirectTo", "changePassfeild", "clickEnter"]),
    async login() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let user = await axios.get(
          `http://localhost:3000/users?name=${this.userName}&password=${this.password}`
        );
        if (user.status == 200 && user.data.length > 0) {
          this.correctData = "loading";
          setTimeout(() => {
            this.correctData = "loading ."
          }, 400)
          setTimeout(() => {
            this.correctData = "loading . ."
          }, 800)
          setTimeout(() => {
            this.correctData = "loading . . ."
          }, 1200)
          this.unCorrectData = "";
          sessionStorage.setItem("user-data", JSON.stringify(user.data));
          setTimeout(() => {
            this.redirectTo("home page");
          }, 1600)
        } else {
          this.unCorrectData = "wrong data";
        }
      }
    },
  },
  data() {
    return {
      v$: useValidate(),
      userName: "",
      password: "",
      unCorrectData: "",
      correctData: "",
    };
  },
  validations() {
    return {
      userName: { required, maxLength: maxLength(15) },
      password: { required, minLength: minLength(10) },
    };
  },
  mounted() {
    document.getElementById('user-name-field-login').focus();
    this.clickEnter()
    let localUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (localUser) {
      this.redirectTo("home page");
    }
  },
};
</script>
