<template>
  <div class="container">
    <nav-bar />
    <div class="delete-box text-capitalize">
      <hr />
      <h5 class="text-danger text-center mb-3" style="font-size: 22px">
        Are you sure deleting this category ?
      </h5>
      <p style="font-size: 17px">
        <span class="fw-bold">name :&MediumSpace;</span>
        <span class="text-center">{{ categoryName }}</span>
      </p>
      <hr />
      <div class="buttons d-flex justify-content-around">
        <router-link
          class="btn btn-info"
          :to="{
            name: 'view categories page',
            params: { restaurantID: restaurantId },
          }"
        >
          cancel
        </router-link>
        <button
          class="click-enter btn btn-danger text-capitalize"
          @click="
            deleteCategory();
            deleteRelatedItems();
          "
        >
          delete
        </button>
      </div>
      <div
        class="success-message alert alert-success text-capitalize text-center mt-3"
        v-if="successDeleting != ''"
      >
        {{ successDeleting }}
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "ViewCategories",
  components: {
    NavBar,
  },
  data() {
    return {
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      categoryId: this.$route.params.categoryId,
      categoryName: "",
      successDeleting: "",
      relatedItemsIds: []
    };
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getCategoryName() {
      let categories = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&id=${this.categoryId}`
      );
      if (categories.status == 200 && categories.data.length > 0) {
        this.categoryName = categories.data[0].name;
      }
    },
    async deleteCategory() {
      let category = await axios.delete(
        `http://localhost:3000/categories/${this.categoryId}`
      );
      if (category.status == 200) {
        this.successDeleting = "deleted successfully";
        setTimeout(() => {
          this.$router.push({
            name: "view categories page",
            params: { restaurantID: this.restaurantId },
          });
        }, 800);
      }
    },
    async deleteRelatedItems() {
      let items = await axios.get(
        `http://localhost:3000/items?userId=${this.userId}&restaurantId=${this.restaurantId}&categoryId=${this.categoryId}`
      );
      if (items.status == 200) {
        for (let i = 0; i < items.data.length; i++) {
          this.relatedItemsIds.push(items.data[i].id);
        }
        let itemsIds = [...this.relatedItemsIds];
        for (let y = 0; y < itemsIds.length; y++) {
          let items = await axios.delete(
            `http://localhost:3000/items/${itemsIds[y]}`
          );
        }
      }
    },
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("view categories page");
    } else {
      this.userId = storageUser[0].id;
      this.getCategoryName();
    }
    this.clickEnter()
  },
};
</script>
