<template>
  <!-- Button trigger modal -->
  <button
    type="button"
    class="btn btn-primary text-capitalize"
    data-bs-toggle="modal"
    data-bs-target="#exampleModal"
    @click="
      clear();
      focus();
    "
  >
    add new restaurant
  </button>

  <!-- Modal -->
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title text-capitalize" id="exampleModalLabel">
            enter restaurant data
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body text-center">
          <div class="input">
            <font-awesome-icon icon="file-signature" />
            <input
              type="text"
              placeholder="name"
              v-model="name"
              id="nameField"
            />
            <span
              v-if="v$.name.$error"
              class="error-message alert alert-danger d-block text-center p-0 border-0"
              >{{ v$.name.$errors[0].$message }}</span
            >
          </div>
          <div class="input">
            <font-awesome-icon icon="location-dot" />
            <input type="text" placeholder="address" v-model="address" />
            <span
              v-if="v$.address.$error"
              class="error-message alert alert-danger d-block text-center p-0 border-0"
              >{{ v$.address.$errors[0].$message }}</span
            >
          </div>
          <div class="password input">
            <font-awesome-icon icon="phone" />
            <input type="tel" placeholder="phone number" v-model="phone" />
          </div>
          <span
            v-if="v$.phone.$error"
            class="error-message alert alert-danger d-block text-center p-0 border-0"
            >{{ v$.phone.$errors[0].$message }}</span
          >
          <span
            class="success-message alert alert-success text-capitalize"
            v-if="successAdding != ''"
            >{{ successAdding }}</span
          >
          <span
            class="success-message alert alert-danger text-capitalize"
            v-if="errorMessage != ''"
            >{{ errorMessage }}</span
          >
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button
            type="button"
            class="click-enter btn btn-primary text-capitalize"
            @click="addRestaurant()"
          >
            add restaurant
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";

import useValidate from "@vuelidate/core";
import { required, maxLength, minLength, numeric } from "@vuelidate/validators";
import axios from "axios";

export default {
  name: "AddRestaurant",
  data() {
    return {
      v$: useValidate(),
      name: "",
      address: "",
      phone: "",
      userId: "",
      successAdding: "",
      errorMessage: "",
      myRstaurantsArr: [],
    };
  },
  validations() {
    return {
      name: { required },
      address: { required },
      phone: {
        required,
        minLength: minLength(11),
        maxLength: maxLength(11),
        numeric,
      },
    };
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async addRestaurant() {
      this.v$.$validate();
      if (!this.v$.$error) {
        if (this.hasBeenDulicated() == true) {
          let restaurant = await axios.post(`http://localhost:3000/locations`, {
            name: this.name,
            address: this.address,
            phone: this.phone,
            userId: this.userId,
          });
          if (restaurant.status == 201) {
            this.name = "";
            this.address = "";
            this.phone = "";
            this.v$.name.$errors[0] = "";
            this.v$.address.$errors[0] = "";
            this.v$.phone.$errors[0] = "";
            this.successAdding = "added succesfully";
            this.errorMessage = "";
            window.location.reload();
          }
        } else {
          this.errorMessage = "this restaurant is dublicated";
        }
      }
    },
    clear() {
      this.name = "";
      this.address = "";
      this.phone = "";
    },
    focus() {
      setTimeout(() => {
        document.getElementById("nameField").focus();
      }, 470);
    },
    hasBeenDulicated() {
      let dublicated = this.myRstaurantsArr.filter(
        (res) =>
          res.name.toLowerCase() == this.name.toLocaleLowerCase() &&
          res.address.toLocaleLowerCase() == this.address.toLocaleLowerCase()
      );
      console.log(this.name);
      if (dublicated.length > 0) {
        console.log("ok");
        return false;
      } else {
        return true;
      }
    },
    async getMyRestaurants() {
      let restaurants = await axios.get(
        `http://localhost:3000/locations?userId=${this.userId}`
      );
      if (restaurants.status == 200 && restaurants.data.length > 0) {
        this.myRstaurantsArr = restaurants.data;
      }
    },
  },
  mounted() {
    let localUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (localUser) {
      this.userId = JSON.parse(sessionStorage.getItem("user-data"))[0].id;
    }
    this.getMyRestaurants();
    this.clickEnter();
  },
  computed: {
    ...mapState(["passStatus"]),
  },
};
</script>

<style scoped lang="scss">
input {
  height: 40px;
  width: 300px;
  padding: 0px 37px;
  font-size: 18px;
  outline: none;
  transition: 0.2s;
  border: 1px solid rgb(133, 133, 133);
  color: #222;

  &:focus {
    box-shadow: 0 0 10px #009688;
  }

  &::placeholder {
    text-transform: capitalize;
  }
}
.input {
  position: relative;
  width: fit-content;
  margin: auto;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  svg {
    position: absolute;
    z-index: 1;
    color: #2196f3;
    font-size: 18px;
    top: 11px;
    left: 11px;
  }
}

.password {
  .eye {
    top: 13px;
    left: calc(100% - 48px);
    font-size: 17px;
    cursor: pointer;
  }
}
.error-message {
  line-height: 55px;
  width: fit-content;
  margin: auto;
  min-width: 300px;
  padding: 0 10px !important;
  &:last-of-type {
    min-width: 300px;
  }
}

.success-message {
  margin: 20px auto 0;
  display: inline-block;
  min-width: 300px;
}
</style>
