<template>
  <div class="container">
    <nav-bar style="margin-bottom: 40px" />
        <router-link :to="{ name: 'view categories page', params: {restaurantID: restaurantId} }" class="btn btn-success"
      >go back</router-link
    >
    <form @submit.prevent class="feilds-container">
      <div class="user">
        <font-awesome-icon icon="file-signature" />
        <input
          id="categoryField"
          type="text"
          placeholder="categorey name"
          v-model="categoryName"
        />
        <span v-if="v$.categoryName.$error" class="error-message">{{
          v$.categoryName.$errors[0].$message
        }}</span>
        <div class="buttons d-flex justify-content-around">
          <button
            type="button"
            class="click-enter btn btn-primary text-capitalize mt-3"
            @click="addCategory()"
          >
            add category
          </button>
        </div>
      </div>
      <div
        class="alert alert-success"
        role="alert"
        v-if="successMessage != ''"
        style="margin: auto; width: 300px"
      >
        {{ successMessage }}
      </div>
      <div
        class="alert alert-danger"
        role="alert"
        v-if="errorMessage != ''"
        style="margin: auto; width: 300px"
      >
        {{ errorMessage }}
      </div>
    </form>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";

export default {
  name: "ViewCategories",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useVuelidate(),
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      categoryName: "",
      myCategoriesArr: [],
      successMessage: "",
      errorMessage: "",
    };
  },
  validations() {
    return {
      categoryName: { required },
    };
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getMyCategories() {
      let categories = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`
      );
      if (categories.status == 200 && categories.data.length > 0) {
        this.myCategoriesArr = categories.data;
      }
    },
    hasBeenDulicated() {
      let dublicated = this.myCategoriesArr.filter(
        (cat) => cat.name.toLowerCase() == this.categoryName.toLowerCase()
      );
      if (dublicated.length > 0) {
        return false;
      } else {
        return true;
      }
    },
    async addCategory() {
      this.v$.$validate();
      if (!this.v$.$error) {
        if (this.hasBeenDulicated() == true) {
          let category = await axios.post(`http://localhost:3000/categories`, {
            name: this.categoryName,
            userId: this.userId,
            restaurantId: this.restaurantId,
          });
          if (category.status == 201) {
            this.successMessage = "added successfully";
            this.errorMessage = '';
            setTimeout(() => {
              this.$router.push({
                name: "view categories page",
                params: { restaurantID: this.restaurantId },
              });
            }, 800);
          }
        }
        else {
          this.errorMessage = "this category is dublicated";
        }
      }
    },
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      document.getElementById("categoryField").focus();
      this.userId = storageUser[0].id;
      this.getMyCategories();
    }
    this.clickEnter()
  },
};
</script>
