<template>
  <div class="container">
    <nav-bar />
    <form class="update-box text-capitalize" @click.prevent>
      <h3 class="text-center">update item</h3>
      <hr />
      <div class="form-floating mb-3">
        <input
          type="text"
          class="form-control"
          id="floatingInput"
          placeholder="name@example.com"
          v-model="itemName"
        />
        <label for="floatingInput">item name is</label>
        <div class="alert alert-danger text-center" v-if="v$.itemName.$error">
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <input
          type="number"
          class="form-control"
          id="floatingPassword"
          placeholder="Password"
          v-model="itemPrice"
        />
        <label for="floatingPassword">item price is</label>
        <div class="alert alert-danger text-center" v-if="v$.itemPrice.$error">
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <input
          type="number"
          class="form-control"
          id="floatingPassword"
          placeholder="Password"
          v-model="itemQuantities"
        />
        <label for="floatingPassword">item quantities is</label>
        <div
          class="alert alert-danger text-center"
          v-if="v$.itemQuantities.$error"
        >
          {{ v$.$errors[0].$message }}
        </div>
      </div>
      <div class="form-floating mb-3">
        <textarea
          class="form-control h200 mb-3"
          placeholder="Leave a comment here"
          id="floatingTextarea"
          v-model="itemDescription"
        ></textarea>
        <label for="floatingTextarea">item description is</label>
        <div
          class="alert alert-danger text-center"
          v-if="v$.itemDescription.$error"
        >
          {{ v$.$errors[0].$message }}
        </div>
        
      </div>
      <button
        class="click-enter text-capitalize btn-warning w-100 add-item-btn mb-3 border"
        @click="updateItem()"
      >
        update item
      </button>
      <div v-if="successMessage != ''" class="alert alert-success text-center">
        {{ successMessage }}
      </div>
    </form>
  </div>
</template>

<script>
import useValidate from "@vuelidate/core";
import { required, maxLength, minValue } from "@vuelidate/validators";
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  name: "UpdateRestaurant",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useValidate(),
      itemName: "",
      itemPrice: "",
      itemQuantities: "",
      itemDescription: "",
      successMessage: "",
      restaurantId: this.$route.params.restaurantId,
      categoryId: this.$route.params.categoryId,
      itemId: this.$route.params.itemId,
      userId: "",
      categoriesArr: [],
    };
  },
  validations() {
    return {
      itemName: { required },
      itemPrice: { required, minValue: minValue(0) },
      itemQuantities: { required, minValue: minValue(1) },
      itemDescription: { required, maxLength: maxLength(50) },
    };
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.getItemtData();
      this.getCategories();
    }
    this.clickEnter();
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getItemtData() {
      let item = await axios.get(
        `http://localhost:3000/items?userId=${this.userId}&restaurantId=${this.restaurantId}&categoryId=${this.categoryId}&id=${this.itemId}`
      );
      if (item.status == 200 && item.data.length > 0) {
        this.itemName = item.data[0].name;
        this.itemPrice = item.data[0].price;
        this.itemQuantities = item.data[0].quantities;
        this.itemDescription = item.data[0].describtion;
      } else {
        this.$router.push({
          name: "menus page",
          params: { restaurantId: this.restaurantId },
        });
      }
    },
    async updateItem() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let item = await axios.patch(
          `http://localhost:3000/items/${this.itemId}`,
          {
            name: this.itemName,
            price: this.itemPrice,
            quantities: this.itemQuantities,
            describtion: this.itemDescription,
            categoryId: this.pickedCategory,
            userId: this.userId,
            restaurantId: this.restaurantId,
          }
        );
        if (item.status == 200) {
          this.successMessage = "updated succesfully";
          setTimeout(
            () =>
              this.$router.push({
                name: "menus page",
                params: { restaurantID: this.restaurantId },
              }),
            800
          );
        }
      }
    },
    async getCategories() {
      let categories = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`
      );
      if (categories.status == 200 && categories.data.length > 0) {
        this.categoriesArr = categories.data;
      }
    },
  },
};
</script>

<style scoped lang="scss">
.update-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
input {
  height: 40px;
  width: 300px;
  padding: 0px 37px;
  font-size: 18px;
  outline: none;
  transition: 0.2s;
  border: 1px solid rgb(133, 133, 133);
  color: #222;

  &:focus {
    box-shadow: 0 0 10px #009688;
  }

  &::placeholder {
    text-transform: capitalize;
  }
}
.input {
  position: relative;
  width: fit-content;
  margin: auto;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  svg {
    position: absolute;
    z-index: 1;
    color: #2196f3;
    font-size: 18px;
    top: 11px;
    left: 11px;
  }
}

.add-item-btn {
  height: 40px;
}

.password {
  .eye {
    top: 13px;
    left: calc(100% - 48px);
    font-size: 17px;
    cursor: pointer;
  }
}
.error-message {
  width: fit-content;
  margin: auto;
  width: 300px;
  padding: 10px !important;
  &:last-of-type {
    min-width: 300px;
  }
}

.success-message {
  margin: 20px auto 0;
  display: inline-block;
  min-width: 300px;
}
</style>
