<template>
  <div class="container">
    <nav-bar />
    <div class="delete-box text-capitalize">
      <h5 class="text-danger text-center mb-3">
        Are you sure deleting this item ?
      </h5>
      <hr>
      <p><span class="fw-bold">name :</span> {{ name }}</p>
      <p><span class="fw-bold">price : </span> {{ price }}</p>
      <p><span class="fw-bold">quantities : </span> {{ quantities }}</p>
      <p><span class="fw-bold">describtion : </span> {{ describtion }}</p>
      <p><span class="fw-bold">category : </span> {{ category }}</p>
      <hr />
      <div class="buttons d-flex justify-content-around">
        <router-link class="btn btn-info" :to="{name: 'menus page', params: {restaurantId: restaurantId}}">
          cancel
        </router-link>
        <button class="click-enter btn btn-danger" @click="deleteItem();">
          Delete
        </button>
      </div>
      <div
        class="success-message alert alert-success text-capitalize text-center mt-3"
        v-if="successAdding != ''"
        >{{ successAdding }}</div
      >
    </div>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import axios from "axios";
import { mapActions } from "vuex";
export default {
  name: "DeleteRestaurant",
  components: {
    NavBar,
  },
  data() {
    return {
      name: "",
      price: "",
      quantities: "",
      describtion: "",
      category: "",
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      categoryId: this.$route.params.categoryId,
      itemId: this.$route.params.itemId,
      successAdding: "",
    };
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.$router.push({name: 'menus page', params: {restaurantId: this.restaurantId}});
    } else {
      this.userId = storageUser[0].id;
      this.getItem();
    }
    this.clickEnter()
  },
  methods: {
    ...mapActions(["clickEnter"]),
    async deleteItem() {
      let restaurant = await axios.delete(
        `http://localhost:3000/items/${this.itemId}`
      );
      if (restaurant.status == 200) {
        this.successAdding = "deleted succesfully";
        setTimeout(() =>
          this.$router.push({name: 'menus page', params: {restaurantId: this.restaurantId}})
          , 800);
      }
    },
    async getCategory() {
      let category = await axios.get(`http://localhost:3000/categories/${this.categoryId}`);
      if (category.status == 200) {
        this.category = category.data.name;
      }
    },
    async getItem() {
      let item = await axios.get(`http://localhost:3000/items?userId=${this.userId}&restaurantId=${this.restaurantId}&categoryId=${this.categoryId}&id=${this.itemId}`);
      if (item.status == 200) {
        this.name = item.data[0].name
        this.price = item.data[0].price
        this.quantities = item.data[0].quantities
        this.describtion = item.data[0].describtion
        this.getCategory();
      }
      else {
        this.$route.push({ name: 'home page' });
      }
    }
  },
};
</script>

<style lang="scss">
.delete-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  width: 378px;
}
</style>
