<template>
  <div class="home">
    <div class="container">
      <nav-bar />
      <add-restaurant />
      <restaurant-show :dataArr="restaurants" v-if="restaurants.length > 0" />
      <div
        class="alert alert-warning mt-3 text-center fs-5 text-capitalize"
        role="alert"
        v-else
      >
        you have not add any restaurant yet !
      </div>
    </div>
  </div>
</template>

<script>
import navBar from "@/components/global/NavBar.vue";
import AddRestaurant from "@/components/home/AddRestaurant.vue";
import RestaurantShow from "@/components/home/RestaurantsShow.vue";
import axios from "axios";
import { mapActions } from "vuex";
export default {
  name: "homeView",
  components: {
    navBar,
    AddRestaurant,
    RestaurantShow,
  },
  methods: {
    ...mapActions(["redirectTo"]),
  },
  data() {
    return {
      restaurants: [],
    };
  },
  async mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("login page");
    } else {
      this.UserId = JSON.parse(sessionStorage.getItem("user-data"))[0].id;
      let gotRestaurants = await axios.get(
        `http://localhost:3000/locations?userId=${this.UserId}`
      );
      this.restaurants = gotRestaurants.data;
    }
  },
};
</script>

<style scoped lang="scss">
nav {
  margin-bottom: 40px;
}
</style>
