<template>
  <div class="container">
    <div class="feilds-container">
      <div class="icon">
        <font-awesome-icon icon="user-plus" class="sign-up" />
      </div>
      <h3>create a new account</h3>
      <div class="user-name">
        <font-awesome-icon icon="user" />
        <input type="text" placeholder="enter your username" v-model="userName" id="user-name-field-signup" />
        <span v-if="v$.userName.$error" class="error-message">{{
          v$.userName.$errors[0].$message
        }}</span>
      </div>
      <div class="email">
        <font-awesome-icon icon="envelope" />
        <input type="email" placeholder="enter your email" v-model="email" />
        <span v-if="v$.email.$error" class="error-message">{{
          v$.email.$errors[0].$message
        }}</span>
      </div>
      <div class="password">
        <font-awesome-icon icon="lock" />
        <input
          v-if="!passStatus"
          type="password"
          placeholder="enter your password"
          v-model="password"
        />
        <input
          v-if="passStatus"
          type="text"
          placeholder="enter your password"
          v-model="password"
        />
        <span v-if="v$.password.$error" class="error-message">{{
          v$.password.$errors[0].$message
        }}</span>
        <div v-if="!passStatus" @click="changePassfeild(true)">
          <font-awesome-icon icon="eye" class="eye show" />
        </div>
        <div v-if="passStatus" @click="changePassfeild(false)">
          <font-awesome-icon icon="eye-slash" class="eye hide" />
        </div>
      </div>
      <div class="submit">
        <button @click="signUp()" class="signup click-enter">sign up</button>
      </div>
      <p class="redirect">
        I have an account, go to
        <router-link :to="{ name: 'login page' }" class="text-decoration-none"
          >login</router-link
        >
        page
      </p>
      <span class="alert alert-danger" v-if="founduserErr != ''">{{
        founduserErr
      }}</span>
      <span class="alert alert-success" v-if="successMessage != ''">{{
        successMessage
      }}</span>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import useValidate from "@vuelidate/core";
import { required, maxLength, minLength, email } from "@vuelidate/validators";
import axios from "axios";

export default {
  name: "SingUpView",
  data() {
    return {
      v$: useValidate(),
      userName: "",
      email: "",
      password: "",
      founduserErr: "",
      successMessage: "",
    };
  },
  validations() {
    return {
      userName: { required, maxLength: maxLength(15) },
      email: { required, email },
      password: { required, minLength: minLength(10) },
    };
  },
  computed: {
    ...mapState(["passStatus"]),
  },
  methods: {
    ...mapActions(["redirectTo", "changePassfeild", "clickEnter"]),
    async signUp() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let checkName = await axios.get(
          `http://localhost:3000/users?name=${this.userName}`
        );
        if (checkName.status == 200) {
          if (checkName.data.length != 1) {
            let checkEmail = await axios.get(
              `http://localhost:3000/users?email=${this.email}`
            );
            if (checkEmail.status == 200) {
              if (checkEmail.data.length != 1) {
                let user = await axios.post("http://localhost:3000/users", {
                  name: this.userName,
                  email: this.email,
                  password: this.password,
                });
                if (user.status == 201) {
                  this.founduserErr = "";
                  this.successMessage = "signed up successfully";
                  setTimeout(() => {
                    this.redirectTo("login page");
                  }, 400);
                }
              }
              else {
                this.founduserErr = "this email is used before"
              }
            }
          } else {
            this.founduserErr = "this username is used before";
          }
        }
      }
    },
  },
  mounted() {
    document.getElementById('user-name-field-signup').focus();
    this.clickEnter()
    let localUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (localUser) {
      this.redirectTo("login page");
    }
  },
};
</script>
