<template>
  <div class="container">
    <nav-bar />
    <form class="update-box text-capitalize" @click.prevent>
      <h3 class="text-center">update category</h3>
      <hr />
      <div class="input">
        <font-awesome-icon icon="file-signature" />
        <input type="text" placeholder="name" v-model="categoryName" />
        <span
          v-if="v$.categoryName.$error"
          class="error-message alert alert-danger d-block text-center border-0"
          >{{ v$.categoryName.$errors[0].$message }}</span
        >
      </div>
      <hr />
      <div class="buttons d-flex justify-content-around">
        <router-link
          class="btn btn-info"
          :to="{
            name: 'view categories page',
            params: { restaurantID: restaurantId },
          }"
        >
          cancel
        </router-link>
        <button class="click-enter btn btn-danger" @click="updateCategory()">update</button>
      </div>
      <div
        class="success-message alert alert-success text-capitalize text-center mt-3"
        v-if="successAdding != ''"
      >
        {{ successAdding }}
      </div>
    </form>
  </div>
</template>

<script>
import useValidate from "@vuelidate/core";
import { required, maxLength, minLength, numeric } from "@vuelidate/validators";
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  name: "UpdateRestaurant",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useValidate(),
      address: "",
      phone: "",
      categoryId: this.$route.params.categoryId,
      restaurantId: this.$route.params.restaurantId,
      categoryName: "",
      userId: "",
      successAdding: "",
    };
  },
  validations() {
    return {
      categoryName: { required },
    };
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.getCategorytData();
    }
    this.clickEnter()
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getCategorytData() {
      let category = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}&id=${this.categoryId}`
      );
      if (category.status == 200 && category.data.length > 0) {
        this.categoryName = category.data[0].name;
      } else {
        this.$router.push({
          name: "view categories page",
          params: { restaurantID: this.restaurantId },
        });
      }
    },
    async updateCategory() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let category = await axios.patch(
          `http://localhost:3000/categories/${this.categoryId}`,
          {
            name: this.categoryName,
            userId: this.userId,
            restaurantId: this.restaurantId,
            id: this.categoryId,
          }
        );
        if (category.status == 200) {
          this.successAdding = "updated succesfully";
          setTimeout(
            () =>
              this.$router.push({
                name: "view categories page",
                params: { restaurantID: this.restaurantId },
              }),
            700
          );
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
.update-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
}
input {
  height: 40px;
  width: 300px;
  padding: 0px 37px;
  font-size: 18px;
  outline: none;
  transition: 0.2s;
  border: 1px solid rgb(133, 133, 133);
  color: #222;

  &:focus {
    box-shadow: 0 0 10px #009688;
  }

  &::placeholder {
    text-transform: capitalize;
  }
}
.input {
  position: relative;
  width: fit-content;
  margin: auto;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  svg {
    position: absolute;
    z-index: 1;
    color: #2196f3;
    font-size: 18px;
    top: 11px;
    left: 11px;
  }
}

.password {
  .eye {
    top: 13px;
    left: calc(100% - 48px);
    font-size: 17px;
    cursor: pointer;
  }
}
.error-message {
  width: fit-content;
  margin: auto;
  width: 300px;
  padding: 10px !important;
  &:last-of-type {
    min-width: 300px;
  }
}

.success-message {
  margin: 20px auto 0;
  display: inline-block;
  min-width: 300px;
}
</style>
