import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import loginView from '../views/login.vue'
import signUpView from "../views/signUp.vue";
import ErrorView from "../views/ErrorView.vue";
import Portfolio from "../views/PortFolio.vue";
import PortfolioUpdate from "../views/PortfolioUpdate.vue";
import DeletePage from "../components/home/DeleteRestaurant.vue";
import UpdatePage from "../components/home/UpdateRestaurant.vue";
import MenusPage from "../components/home/RestaurantMenus.vue";
import ViewCategories from "../components/categories/ViewCategories.vue";
import AddCategory from "../components/categories/AddCategory.vue";
import DeleteCategory from "../components/categories/DeleteCategory.vue";
import UpdateCategory from "../components/categories/UpdateCategory.vue";
import AddItems from "../components/items/AddItems.vue";
import DeleteItem from "../components/items/DeleteItem.vue";
import UpdateItem from "../components/items/UpdateItem.vue";

const routes = [
  {
    path: "/",
    name: "home page",
    component: HomeView,
  },
  {
    path: "/login",
    name: "login page",
    component: loginView,
  },
  {
    path: "/signup",
    name: "sign up page",
    component: signUpView,
  },
  {
    path: "/portfolio",
    name: "portfolio page",
    component: Portfolio,
  },
  {
    path: "/portfolio/update",
    name: "portfolio update page",
    component: PortfolioUpdate,
  },
  {
    path: "/restaurant/delete/:restaurantId-:restaurantOrder",
    name: "delete page",
    component: DeletePage,
  },
  {
    path: "/restaurant/update/:restaurantId-:restaurantOrder",
    name: "update page",
    component: UpdatePage,
  },
  {
    path: "/restaurant/:restaurantId/menus",
    name: "menus page",
    component: MenusPage,
  },
  {
    path: "/restaurant/:restaurantID/menus/categories/view",
    name: "view categories page",
    component: ViewCategories,
  },
  {
    path: "/restaurant/:restaurantId/menus/categories/add",
    name: "add category page",
    component: AddCategory,
  },
  {
    path: "/restaurant/:restaurantId/menus/categories/delete/:categoryId",
    name: "delete category page",
    component: DeleteCategory,
  },
  {
    path: "/restaurant/:restaurantId/menus/categories/update/:categoryId",
    name: "update category page",
    component: UpdateCategory,
  },
  {
    path: "/restaurant/:restaurantId/menus/items/add",
    name: "add items page",
    component: AddItems,
  },
  {
    path: "/restaurant/:restaurantId/menus/category/:categoryId/items/:itemId/delete",
    name: "delete item page",
    component: DeleteItem,
  },
  {
    path: "/restaurant/:restaurantId/menus/category/:categoryId/items/:itemId/update",
    name: "update item page",
    component: UpdateItem,
  },
  {
    path: "/:catchAll(.*)*/",
    name: "error page",
    component: ErrorView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach(function (to) {
  document.title = to.name;
})

export default router
