<template>
  <div class="container">
    <nav-bar style="margin-bottom: 40px" />
    <div class="buttons mb-3">
      <router-link
        class="text-capitalize btn btn-primary"
        :to="{
          name: 'view categories page',
          params: { restaurantID: restaurantId },
        }"
        >view categories</router-link
      >
      <router-link :to="{ name: 'home page' }" class="btn btn-success ms-2"
        >go back</router-link
      >
      <router-link
        class="text-capitalize btn btn-primary float-end"
        :to="{ name: 'add items page', params: { restaurantId: restaurantId } }"
        v-if="categoriesArr.length > 0"
        >add items</router-link
      >
      <div class="clearfix"></div>
    </div>
    <div class="text-capitalize">
      <div
        class="alert alert-warning mt-3 text-center fs-5 text-capitalize"
        role="alert"
        v-if="categoriesArr.length == 0"
      >
        you must add one category at least
      </div>
      <div class="content" v-else>
        <div
          class="category text-capitalize"
          v-for="cat in categoriesArr"
          :key="cat.id"
        >
          <div class="category-title mb-3">
            <h4 class="mb-0">{{ cat.name }}</h4>
          </div>
          <div class="items d-flex flex-wrap">
            <template
              v-for="item in itemsArr"
              :key="item.categoryId"
            >
              <div v-if="cat.id == item.categoryId" class="item">
                <h4 class="mb-3">{{ item.name }}</h4>
                <p class="describtion">{{ item.describtion }}</p>
                <p class="d-flex justify-content-between">
                  <span>available qty : {{ item.quantities }}</span
                  ><span>price : {{ item.price < 1000 ? Number(item.price).toFixed(2) : Number(item.price).toLocaleString() }} $</span>
                </p>
                <div class="buttons d-flex justify-content-between">
                  <router-link class="btn btn-primary" :to="{name: 'update item page', params: {itemId: item.id, restaurantId: restaurantId, categoryId: cat.id}}">update</router-link>
                  <router-link class="btn btn-danger" :to="{name: 'delete item page', params: {itemId: item.id, restaurantId: restaurantId, categoryId: cat.id}}">delete</router-link>
                </div>
                <hr />
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "RestaurantMenus",
  components: {
    NavBar,
  },
  data() {
    return {
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      categoriesArr: [],
      itemsArr: [],
    };
  },
  methods: {
    ...mapActions(["redirectTo"]),
    async getCategories() {
      let categories = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`
      );
      if (categories.status == 200 && categories.data.length > 0) {
        this.categoriesArr = categories.data;
      }
    },
    async getItems() {
      let items = await axios.get(
        `http://localhost:3000/items?userId=${this.userId}&restaurantId=${this.restaurantId}`
      );
      if (items.status == 200 && items.data.length > 0) {
        this.itemsArr = items.data;
      }
    },
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.getCategories();
      this.getItems();
    }
  },
};
</script>

<style lang="scss">
.category {
  .category-title {
    background-color: #eee;
    text-align: center;
    padding: 10px;
  }
  .items {
    column-gap: 55px;
    row-gap: 30px;
    .item {
      width: 30%;
      display: inline-block;
      .describtion {
        color: #555;
      }
    }
  }
}
</style>
