<template>
  <div class="container">
    <nav-bar />
    <div class="feilds-container">
      <div class="icon">
        <font-awesome-icon icon="address-card" />
      </div>
      <h3>profile data</h3>
      <div class="user-name">
        <font-awesome-icon icon="user" />
        <input
          type="text"
          placeholder="enter your name"
          v-model="userName"
          disabled
          style="color: #777"
        />
      </div>
      <div class="email">
        <font-awesome-icon icon="envelope" />
        <input
          type="email"
          placeholder="enter your email"
          v-model="email"
          disabled
          style="color: #777"
        />
      </div>
      <div class="password">
        <font-awesome-icon icon="lock" />
        <input
          v-if="!passStatus"
          type="password"
          placeholder="enter your password"
          v-model="password"
          disabled
          style="color: #777"
        />
        <input
          v-if="passStatus"
          type="text"
          placeholder="enter your password"
          v-model="password"
          disabled
          style="color: #777"
        />
        <div v-if="!passStatus" @click="changePassfeild(true)">
          <font-awesome-icon icon="eye" class="eye show" />
        </div>
        <div v-if="passStatus" @click="changePassfeild(false)">
          <font-awesome-icon icon="eye-slash" class="eye hide" />
        </div>
      </div>
      <div class="submit">
        <button @click="redirectTo('portfolio update page')" class="update">
          update profile data
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/global/NavBar.vue";
import { mapActions, mapState } from "vuex";

export default {
  name: "Portfolio",
  components: {
    NavBar,
  },
  data() {
    return {
      userName: "",
      email: "",
      password: "",
    };
  },
  methods: {
    ...mapActions(["redirectTo", "changePassfeild"]),
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("login page");
    } else {
      this.userName = storageUser[0].name;
      this.email = storageUser[0].email;
      this.password = storageUser[0].password;
      this.id = storageUser[0].id;
    }
  },
  computed: {
    ...mapState(["passStatus"]),
  },
};
</script>

<style></style>
