      <tr>
        <th>id</th>
        <th>name</th>
        <th>address</th>
        <th>phone</th>
        <th>controls</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(ele, i) in dataArr" :key="i">
        <td class="align-middle fw-bold">{{ ++i }}</td>
        <td class="align-middle">{{ ele.name }}</td>
        <td class="align-middle">{{ ele.address }}</td>
        <td class="align-middle">{{ ele.phone }}</td>
        <td class="align-middle">
          <router-link
            class="btn btn-danger text-center p-0"
            :to="{
              name: 'delete page',
              params: { restaurantId: ele.id, restaurantOrder: i },
            }"
            style="
              font-size: 15px;
              margin-right: 7px;
              width: 68px;
              height: 30px;
              line-height: 28px;
            "
            >delete</router-link
          >
          <router-link
            class="btn btn-dark text-center p-0"
            :to="{
              name: 'update page',
              params: { restaurantId: ele.id, restaurantOrder: i },
            }"
            style="
              font-size: 15px;
              margin-right: 7px;
              width: 68px;
              height: 30px;
              line-height: 28px;
            "
            >update</router-link
          >
          <router-link
            class="btn btn-primary text-center p-0"
            :to="{ name: 'menus page', params: { restaurantId: ele.id } }"
            style="
              font-size: 15px;
              margin-right: 7px;
              width: 68px;
              height: 30px;
              line-height: 28px;
            "
            >menus</router-link
          >
        </td>
      </tr>
    </tbody>
  </table>
