import { createStore } from "vuex";
import router from "@/router";

export default createStore({
  state: {
    passStatus: false,
  },
  getters: {},
  mutations: {
    redirectTo(state, toPage) {
      router.push({ name: toPage });
    },
    changePassfeild(state, iconstatus) {
      state.passStatus = iconstatus;
    },
    clickEnter(state) {
      document.onkeyup = function (e) {
        if (e.key == "Enter") {
          document.getElementsByClassName("click-enter")[0].click();
        }
      };
    },
  },
  actions: {
    redirectTo({ commit }, toPage) {
      commit("redirectTo", toPage);
    },
    changePassfeild({ commit }, iconstatus) {
      commit("changePassfeild", iconstatus);
    },
    clickEnter({ commit }) {
      commit("clickEnter");
    },
  },
  modules: {},
});
