<template>
  <div class="container">
    <nav-bar />
    <div class="update-box text-capitalize">
      <h1 class="text-center">update restaurant #{{ restaurantOrder }}</h1>
      <hr />
      <div class="input">
            <font-awesome-icon icon="file-signature" />
            <input type="text" placeholder="name" v-model="name"/>
            <span
              v-if="v$.name.$error"
              class="error-message alert alert-danger d-block text-center border-0"
              >{{ v$.name.$errors[0].$message }}</span
            >
          </div>
          <div class="input">
            <font-awesome-icon icon="location-dot" />
            <input type="text" placeholder="address" v-model="address" />
            <span
              v-if="v$.address.$error"
              class="error-message alert alert-danger d-block text-center border-0"
              >{{ v$.address.$errors[0].$message }}</span
            >
          </div>
          <div class="password input">
            <font-awesome-icon icon="phone" />
            <input type="tel" placeholder="phone number" v-model="phone" />
          <span
            v-if="v$.phone.$error"
            class="error-message alert alert-danger d-flex justify-content-center align-items-center text-center border-0"
            >{{ v$.phone.$errors[0].$message }}</span
          >
          </div>
      <hr />
      <div class="buttons d-flex justify-content-around">
        <button class="btn btn-secondary" @click="redirectTo('home page')">
          cancel
        </button>
        <button class="click-enter btn btn-danger" @click="updateRestaurant()">
          update
        </button>
      </div>
      <div
        class="success-message alert alert-success text-capitalize text-center mt-3"
        v-if="successAdding != ''"
      >
        {{ successAdding }}
      </div>
    </div>
  </div>
</template>

<script>
import useValidate from "@vuelidate/core";
import { required, maxLength, minLength, numeric } from "@vuelidate/validators";
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  name: "UpdateRestaurant",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useValidate(),
      name: "",
      address: "",
      phone: "",
      restaurantOrder: this.$route.params.restaurantOrder,
      userId: "",
      restaurantId: "",
      successAdding: "",
    };
  },
  validations() {
    return {
      name: { required },
      address: { required },
      phone: {
        required,
        minLength: minLength(11),
        maxLength: maxLength(11),
        numeric,
      },
    };
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.restaurantId = this.$route.params.restaurantId;
      this.getRestaurantData();
      this.clickEnter();
    }
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getRestaurantData() {
      let restaurant = await axios.get(
        `http://localhost:3000/locations?id=${this.restaurantId}&userId=${this.userId}`
      );
      if (restaurant.status == 200 && restaurant.data.length > 0) {
        this.name = restaurant.data[0].name;
        this.address = restaurant.data[0].address;
        this.phone = restaurant.data[0].phone;
      } else {
        this.redirectTo("home page");
      }
    },
    async updateRestaurant() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let restaurant = await axios.patch(
          `http://localhost:3000/locations/${this.restaurantId}`, {
            name: this.name,
            address: this.address,
            phone: this.phone,
        });
        if (restaurant.status == 200) {
          this.successAdding = "updated succesfully";
          setTimeout(() => this.redirectTo("home page"), 700);
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
.update-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
}
input {
  height: 40px;
  width: 300px;
  padding: 0px 37px;
  font-size: 18px;
  outline: none;
  transition: 0.2s;
  border: 1px solid rgb(133, 133, 133);
  color: #222;

  &:focus {
    box-shadow: 0 0 10px #009688;
  }

  &::placeholder {
    text-transform: capitalize;
  }
}
.input {
  position: relative;
  width: fit-content;
  margin: auto;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  svg {
    position: absolute;
    z-index: 1;
    color: #2196f3;
    font-size: 18px;
    top: 11px;
    left: 11px;
  }
}

.password {
  .eye {
    top: 13px;
    left: calc(100% - 48px);
    font-size: 17px;
    cursor: pointer;
  }
}
.error-message {
  width: fit-content;
  margin: auto;
  width: 300px;
  padding: 10px !important;
  &:last-of-type {
    min-width: 300px;
  }
}

.success-message {
  margin: 20px auto 0;
  display: inline-block;
  min-width: 300px;
}
</style>
