<template>
  <div class="container">
    <nav-bar />
    <div class="feilds-container">
      <div class="icon">
        <font-awesome-icon icon="pen" style="font-size: 28px" />
      </div>
      <h3>update profile data</h3>
      <div class="user-name">
        <font-awesome-icon icon="user" />
        <input type="text" placeholder="enter your name" v-model="userName" />
        <span v-if="v$.userName.$error" class="error-message">{{
          v$.userName.$errors[0].$message
        }}</span>
      </div>
      <div class="email">
        <font-awesome-icon icon="envelope" />
        <input type="email" placeholder="enter your email" v-model="email" />
        <span v-if="v$.email.$error" class="error-message">{{
          v$.email.$errors[0].$message
        }}</span>
      </div>
      <div class="password">
        <font-awesome-icon icon="lock" />
        <input
          v-if="!passStatus"
          type="password"
          placeholder="enter your password"
          v-model="password"
        />
        <input
          v-if="passStatus"
          type="text"
          placeholder="enter your password"
          v-model="password"
        />
        <span v-if="v$.password.$error" class="error-message">{{
          v$.password.$errors[0].$message
        }}</span>
        <div v-if="!passStatus" @click="changePassfeild(true)">
          <font-awesome-icon icon="eye" class="eye show" />
        </div>
        <div v-if="passStatus" @click="changePassfeild(false)">
          <font-awesome-icon icon="eye-slash" class="eye hide" />
        </div>
      </div>
      <div class="submit">
        <button @click="updateData()" class="update click-enter">update</button>
      </div>
      <div class="alert alert-success" v-if="successMessage != ''">{{ successMessage }}</div>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/global/NavBar.vue";
import { mapActions, mapState } from "vuex";
import useValidate from "@vuelidate/core";
import { required, maxLength, minLength, email } from "@vuelidate/validators";
import axios from "axios";

export default {
  name: "Portfolio",
  components: {
    NavBar,
  },
  data() {
    return {
      v$: useValidate(),
      userName: "",
      email: "",
      password: "",
      id: "",
      successMessage: "",
    };
  },
  validations() {
    return {
      userName: { required, maxLength: maxLength(15) },
      email: { required, email },
      password: { required, minLength: minLength(10) },
    };
  },
  methods: {
    async updateData() {
      this.v$.$validate();
      if (!this.v$.$error) {
        let user = await axios.put(`http://localhost:3000/users/${this.id}`, {
          name: this.userName,
          email: this.email,
          password: this.password,
        });
        if (user.status == 200) {
          sessionStorage.setItem("user-data", JSON.stringify([user.data]));
          this.successMessage = "updated successfully";
          setTimeout(() => {
            this.redirectTo("home page");
          }, 800)
        }
      }
    },
    ...mapActions(["redirectTo", "changePassfeild", "clickEnter"]),
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("login page");
    } else {
      this.userName = storageUser[0].name;
      this.email = storageUser[0].email;
      this.password = storageUser[0].password;
      this.id = storageUser[0].id;
    }
    this.clickEnter()
  },
  computed: {
    ...mapState(["passStatus"]),
  },
};
</script>
