<template>
  <div class="container">
    <nav-bar style="margin-bottom: 86px" />
    <div class="categories">
      <table
        class="table caption-top text-right position-relative mt-1 text-center"
      >
        <caption
          class="position-absolute fw-500 text-capitalize $gray-700 w-100"
          style="top: -55px"
        >
          <router-link
            :to="{ name: 'add category page', params: {restaurantId: RestaurantId} }"
            class="btn btn-primary ms-1"
            >add a category</router-link
          >
          <router-link
            :to="{ name: 'menus page', params: { restaurantId: RestaurantId } }"
            class="btn btn-success ms-2"
            >go to menus</router-link
          >
          <span class="float-end mt-2">
            restaurant categoreis ({{ categoriesArr.length }})
          </span>
        </caption>
        <thead class="table-dark">
          <tr>
            <th>id</th>
            <th>name</th>
            <th>controls</th>
          </tr>
        </thead>
        <tbody>
          <div class="alert alert-warning mt-3 text-center fs-5 text-capitalize position-absolute w-100 mb-1" v-if="categoriesArr.length == 0">you did not add any category yet</div>
          <tr v-for="(cat, i) of categoriesArr" :key="i">
            <td class="align-middle fw-bold">{{ ++i }}</td>
            <td class="align-middle">{{ cat.name }}</td>
            <td class="align-middle">
              <router-link
                class="btn btn-danger text-center p-0"
                :to="{
                  name: 'delete category page',
                  params: { categoryId: cat.id, restaurantId: RestaurantId },
                }"
                style="
                  font-size: 15px;
                  margin-right: 7px;
                  width: 68px;
                  height: 30px;
                  line-height: 28px;
                "
                >delete</router-link
              >
              <router-link
                class="btn btn-primary text-center p-0"
                :to="{
                  name: 'update category page',
                  params: { restaurantId: RestaurantId, categoryId: cat.id, },
                }"
                style="
                  font-size: 15px;
                  margin-right: 7px;
                  width: 68px;
                  height: 30px;
                  line-height: 28px;
                "
                >update</router-link
              >
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "ViewCategories",
  components: {
    NavBar,
  },
  data() {
    return {
      userId: "",
      RestaurantId: this.$route.params.restaurantID,
      restaurantName: "",
      categoriesArr: [],
    };
  },
  methods: {
    ...mapActions(["redirectTo"]),
    async getCategories() {
      let categories = await axios.get(
        `http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.RestaurantId}`
      );
      if (categories.status == 200 && categories.data.length > 0) {
        this.categoriesArr = categories.data;
      }
    },
    async getResaurantName() {
      let restaurant = await axios.get(
        `http://localhost:3000/locations?id=${this.RestaurantId}&userId=${this.userId}`
      );
      if (restaurant.status == 200 && restaurant.data.length > 0) {
        this.restaurantName = restaurant.data[0].name;
      } else {
        this.redirectTo("home page");
      }
    },
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.getCategories();
      this.getResaurantName();
    }
  },
};
</script>
