<template>
  <div class="container">
    <nav-bar />
    <div class="delete-box text-capitalize">
      <h1 class="text-center">delete restaurant #{{ restaurantOrder }}</h1>
      <hr />
      <h5 class="text-danger text-center mb-3">
        Are you sure deleting this restaurant ?
      </h5>
      <p><span class="fw-bold">name :</span> {{ name }}</p>
      <p><span class="fw-bold">address : </span> {{ address }}</p>
      <p><span class="fw-bold">phone : </span> {{ phone }}</p>
      <hr />
      <div class="buttons d-flex justify-content-around">
        <button class="btn btn-info" @click="redirectTo('home page')">
          cancel
        </button>
        <button class="click-enter btn btn-danger" @click="deleteRestaurant(); deleteRelatedCategories(); deleteRelatedItems()">
          Delete
        </button>
      </div>
      <div
        class="success-message alert alert-success text-capitalize text-center mt-3"
        v-if="successAdding != ''"
        >{{ successAdding }}</div
      >
    </div>
  </div>
</template>

<script>
import NavBar from "../global/NavBar.vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  name: "DeleteRestaurant",
  components: {
    NavBar,
  },
  data() {
    return {
      name: "",
      address: "",
      phone: "",
      restaurantOrder: this.$route.params.restaurantOrder,
      userId: "",
      restaurantId: this.$route.params.restaurantId,
      successAdding: "",
      relatedCategoriesIds: [],
      relatedItemsIds: [],
    };
  },
  mounted() {
    let storageUser = JSON.parse(sessionStorage.getItem("user-data"));
    if (!storageUser) {
      this.redirectTo("home page");
    } else {
      this.userId = storageUser[0].id;
      this.getRestaurantData();
    }
    this.clickEnter()
  },
  methods: {
    ...mapActions(["redirectTo", "clickEnter"]),
    async getRestaurantData() {
      let restaurant = await axios.get(
        `http://localhost:3000/locations?id=${this.restaurantId}&userId=${this.userId}`
      );
      if (restaurant.status == 200 && restaurant.data.length > 0) {
        this.name = restaurant.data[0].name;
        this.address = restaurant.data[0].address;
        this.phone = restaurant.data[0].phone;
      } else {
        this.redirectTo("home page");
      }
    },
    async deleteRestaurant() {
      let restaurant = await axios.delete(
        `http://localhost:3000/locations/${this.restaurantId}`
      );
      if (restaurant.status == 200) {
        this.successAdding = "deleted succesfully";
        setTimeout(() =>
          this.redirectTo('home page')
          , 1000);
      }
    },
    async deleteRelatedCategories() {
      let categories = await axios.get(`http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`);
      if (categories.status == 200) {
        for (let i = 0; i < categories.data.length; i++) {
          this.relatedCategoriesIds.push(categories.data[i].id)
        }
        let catsIds = [...this.relatedCategoriesIds];
        for (let i = 0; i < catsIds.length; i++) {
          let categories = await axios.delete(`http://localhost:3000/categories/${catsIds[i]}`);
        }
      }
    },
    async deleteRelatedItems() {
      let items = await axios.get(`http://localhost:3000/categories?userId=${this.userId}&restaurantId=${this.restaurantId}`);
      if (items.status == 200) {
        for (let i = 0; i < items.data.length; i++) {
          this.relatedItemsIds.push(items.data[i].id)
        }
        let itemsIds = [...this.relatedItemsIds];
        for (let y = 0; y < itemsIds.length; y++) {
          let items = await axios.delete(`http://localhost:3000/items/${itemsIds[y]}`);
        }
      }
    },
  },
};
</script>

<style lang="scss">
.delete-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  width: 378px;
}
</style>
