<template>
  <nav>
    <div class="links">
      <router-link to="/">Home</router-link>
      <router-link :to="{name: 'portfolio page'}">profile</router-link>
      <a @click="logOut()">logout</a>
    </div>
  </nav>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "navBar",
  methods: {
    ...mapActions(["redirectTo"]),
    logOut() {
      sessionStorage.clear();
      this.redirectTo("login page");
    },
  },
};
</script>

<style lang="scss">
nav {
  position: relative;
  text-align: center;
  user-select: none;
  .links {
    a {
      text-decoration: none;
      color: #333;
      padding: 10px 0px;
      background-color: #fff;
      display: inline-block;
      font-weight: bold;
      text-transform: capitalize;
      border-radius: 30px;
      margin: 10px 5px 0;
      width: 100px;
      transition: 0.3s;
      cursor: pointer;
      &:hover {
        background-color: rgb(33 150 243 / 30%);
      }
      &.router-link-exact-active {
        background-color: #2196f3;
        color: white;
      }
    }
  }
}
</style>
